<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="false" :listType="[DetailListType]" ModuleName="Timer"
            SubModuleCode="Timer" IdentityColumn="category_id" HeaderText="History" :ListData="ApplicationCategoryHistoryList"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>

    
<script>
import Schema from '../TimeTracking/HistorySchema';
import DataService from '../../services/DataService';
export default {

    data() {
        return {
            ApplicationCategoryHistoryList: [],
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [

                {
                    "COLUMN_NAME": 'application_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApplicationWebsite'),
                    "DISPLAY_ORDER": 1

                },
                {
                    "COLUMN_NAME": 'Category',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Category'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": 'PriviousCategory',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Previouscategory'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'UserName',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Changedby'),
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": 'modified_at',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Lastupdated'),
                    "DISPLAY_ORDER": 5,
                    // settings: {
                    //     isInSlot: true,
                    // },
                }

            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: '',
            sortExp: '',
            pageSize: 10,
            pageNum: 1,
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }

            ],
            LegendArray: [],
            PageSizeOption: null,
            listheaderbuttons: Schema.buttons,
            isLoading: false,
            DetailListType: 'List',
        }
    },
    created() {
        this.FetchData();
        Schema.bindleftCommonSearchdropdown(this, "Timer", "USERS");
        Schema.bindleftCommonSearchdropdown(this, "Timer", "Category");

    },
    methods: {
        async FetchData() {
            debugger
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&${vueObj.searchCondition}`;
            await DataService.GetTimeTrackingHistoryListing(url).then(response => {
                vueObj.ApplicationCategoryHistoryList = response.data.data;              
                vueObj.isLoading = false;
                vueObj.TotalRecords = response.data.data[0].TotalRecords;
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                vueObj.CurrentPage = vueObj.pageNum;
                vueObj.noRecord = false;
                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                vueObj.isLoading = false;
            })
            setTimeout(function () {
                vueObj.CheckBoxBootstrap();
            }, 100);

        },

        actionButtonClick(callbackfunction, item) {
        },
        renderActions(row) {
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNum;
            this.FetchData();
        },
        Backtolist: function () {
            this.$router.push({
                name: 'UsedApplicationListing'
            });
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Backtolist":
                    this.Backtolist()
                    break;
            }
        },

        leftCommonSearch: function (value) {
            debugger
            this.PageNum = value.PageNum;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },

    }
}
</script>
